<template>
	<div class="search">
		<!-- 搜索区域 -->
		<!-- @input：只要输入框中的内容发生了改变，就得到内容提交到服务器去获取联想的结果 -->
		<van-search v-model="value" @search="onSearch" @cancel="onCancel" @input="think" show-action shape="round" background="#3296fa" placeholder="请输入搜索关键词" />
		<!-- 联想区域 -->
		<van-cell-group v-if="thinkList.length!==0">
			<van-cell title="联想区域" />
			<van-cell v-for="(item,index) in thinkList" @click="onSearch(item.oldItem)" :key="index" icon="search">
				<template #title>
					<!-- 如果要将value中的内容当做html来解析，需要使用到指令:v-html -->
					<div v-html="item.newItem"></div>
				</template>
			</van-cell>
		</van-cell-group>
		<!-- 历史区域 -->
		<van-cell-group v-else>
			<van-cell title="历史区域">
				<template #right-icon>
					<van-icon name="delete" @click="delAllHistory()" />
				</template>
			</van-cell>
			<van-cell :title="item" @click="onSearch(item)" v-for="(item,index) in historyList" :key="index" icon="search">
				<template #default>
					<van-icon @click.stop="delHistory(index)" name="cross" />
				</template>
			</van-cell>
		</van-cell-group>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				value: '',
				thinkList: [],
				historyList: [],
			};
		},
		methods: {
			// 点击手机上的完成按钮时执行(谷歌浏览器模拟的手机，所以就是点击回车)
			onSearch(val) {
				// 使用unshift而不是push，是因为要让用于最近的搜索排在历史列表前面
				this.historyList.unshift(val)
				var newHistoryList = JSON.stringify(this.historyList)
				window.localStorage.setItem('history', newHistoryList)
				// val为输入框中的内容
				// 跳转到搜索页面
				// 路由跳转
				this.$router.push('/searchResult?key=' + val)
			},
			// 点击取消按钮时触发
			onCancel() {
				console.log("取消")
			},
			// 添加联想方法
			// 只要输入框中的内容发生了改变，就得到内容提交到服务器去获取联想的结果
			think() {
				// 1.将输入框的内容提交到服务器
				if (this.value.length === 0) {
					this.thinkList = []
					// retrun一旦执行，就会停止方法的执行
					return
				}
				this.$http({
					url: 'http://ttapi.research.itcast.cn/app/v1_0/suggestion',
					method: "GET",
					params: {
						q: this.value
					}
				}).then(res => {
					// 获取联想结果，并保存到thinkList中
					this.thinkList = res.data.data.options
					// 将所有的关键字全部用span包裹起来
					// 由于span包裹的内容和不包裹的内容都要被用到
					// 使用对象将两个都保存起来
					this.thinkList = this.thinkList.map(item => {
						return {
							oldItem: item,
							newItem: item.replace(this.value, '<span style="color:red">' + this.value + '</span>')
						}

					})
				})

			},
			// 点叉叉删除一条历史记录
			delHistory(index) {
				this.historyList.splice(index, 1)
				var newHistoryList = JSON.stringify(this.historyList)
				window.localStorage.setItem('history', newHistoryList)

			},
			// 点垃圾桶删除全部历史记录
			delAllHistory() {
				this.$dialog.confirm({
					title: '注意！',
					message: '您确定要清空历史记录吗',
				}).then(() => {
					// 点击确认则清空历史记录
					this.historyList = []
					var newHistoryList = JSON.stringify(this.historyList)
					window.localStorage.setItem('history', newHistoryList)
				}).catch(() => {
					console.log("取消")
				});
			}

		},
		created() {
			// 得到localStorage中的历史数据
			const historyInfo = JSON.parse(window.localStorage.getItem('history'))
			// 将historyInfo赋值给historyList，如果historyInfo不存在，就将[]赋值给他
			//  || 短路运算符
			// 对历史列表里的数据进行去重，避免用户输入同样的内容却多次显示
			this.historyList = [...new Set(historyInfo)] || []
		}
	}
</script>

<style scoped>
	.search>>>.van-search__action {
		color: #fff;
	}

	.search>>>.van-search__action:active {
		background-color: #3296fa;
	}
</style>
