//导入Vue 
import Vue from 'vue'
// 导入App
import App from './App.vue'

// 导入VueRouter
import VueRouter from 'vue-router'

// 导入组件
import test from './components/test.vue'
import login from './views/login/login.vue'
import home from './views/home/home.vue'
import index from './views/home/index/index.vue'
import search from './views/home/search/search.vue'
import video from './views/home/video/video.vue'
import my from './views/home/my/my.vue'
import searchResult from './views/searchResult.vue'
import details from './views/details/details.vue'

// 导入vant
import Vant from 'vant'
// 导入vant的样式
import 'vant/lib/index.css'

// 导入字体图标
import './style/index.css'

//导入axios
import axios from 'axios'

// 导入dayjs
import dayjs from 'dayjs'
// 导入相对时间的插件
import relativeTime from 'dayjs/plugin/relativeTime'
// 将相对时间的插件使用到 dayjs 中
dayjs.extend(relativeTime)
// 导入中文语言包
import 'dayjs/locale/zh-cn'
// 全局使用语言包，这之后得到的相对时间都会转换为中文
dayjs.locale('zh-cn')

// 使用VueRouter
Vue.use(VueRouter)

// 定义路由
const routes=[
	{path:'/test',component:test},
	{path:'/login',component:login},
	{path:'/home',component:home,
	// 设置home的子组件的路由
	children:[
		{path:'/index',component:index},
		{path:'/search',component:search},
		{path:'/video',component:video},
		{path:'/my',component:my}
	]},
	{path:'/searchResult',component:searchResult},
	{path:'/details',component:details}
	
]

// 定义一个router对象
const router=new VueRouter({
	routes
})

// 使用 vant
Vue.use(Vant)

//将axios挂载到原型中
Vue.prototype.$http=axios

//使用dayjs
console.log('-------------------使用dayjs------------------')
var time="2019-10-23T09:46:54"
// 格式化时间
var newTime= dayjs(time).format('YYYY-MM-DD hh:mm:ss')
console.log(newTime)

// 得到相对时间
var mytime=dayjs().from(dayjs(time))
console.log(mytime)
console.log('-------------------使用dayjs------------------')

// 定义过滤器:将获得相对时间的方法封装
Vue.filter("timeFilter",function(time){
	return dayjs().from(dayjs(time))
})

// 创建了一个vue实例
new Vue({
	// 将App组件渲染到vue实例中
  render: h => h(App),
  // 挂载router对象
  router
}).$mount('#app')
