<template>
	<div class="search">
		<!-- 头部导航 -->
		<van-nav-bar left-arrow @click-left="onClickLeft" class="login_nav" :fixed="true" title="搜索结果" />
		<!-- 文章列表 -->
		<van-pull-refresh v-model="isLoading" @refresh="onRefresh">
			<van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
				<van-cell v-for="(subItem,subIndex) in searchResult" :key="subIndex">
					<template #title>
						<!-- 标题 -->
						<h4 @click.stop="getDetails(subItem.art_id)">{{subItem.title}}</h4>
						<!-- 图片区域 -->
						<van-grid  v-if="subItem.cover.type!=0" :border="false" :column-num="3">
							<van-grid-item @click.stop="getDetails(subItem.art_id)" v-for="(imageitem,imageindex) in subItem.cover.images" :key="imageindex">
								<van-image :src="imageitem" />
							</van-grid-item>
						</van-grid>
						<!-- 其他信息 -->
						<div class="other">
							<span>{{subItem.aut_name}}</span>
							<span>{{subItem.comm_count}}</span>
							<span>{{subItem.pubdate | timeFilter}}</span>
							<!-- 点击收藏时，如果点击的是当前文章，则显示本图标 -->
							<span v-if="istarget===subItem.art_id&&isCollect===true" @click="collect(subItem.art_id)"><i class="iconfont icon-shoucang"></i></span>
							<!-- 点击收藏时，如果不是当前点击的文章，显示本图标(或未点击时) -->
							<span v-if="isCollect===false||istarget!==subItem.art_id" @click.stop="collect(subItem.art_id)"><i class="iconfont icon-shoucang1"></i></span>
						</div>
						<van-grid direction="horizontal" :column-num="3">
							<van-grid-item icon="friends-o" text="留言"/>
							<van-grid-item icon="like-o" text="点赞" />
							<van-grid-item icon="exchange" text="分享"/>
						</van-grid>
					</template>
				</van-cell>
			</van-list>
		</van-pull-refresh>
	</div>
</template>

<script>
	// 导入json-bigint
	import jsonbigint from 'json-bigint'
	export default {
		data() {
			return {
				searchResult: [],
				loading: false,
				finished: false,
				isLoading: false,
				// 定义一个页数
				page:1,
				per_page:10,
				// 是否收藏,
				isCollect:false,
				istarget:''

			};
		},
		created() {},
		methods: {
			onClickLeft() {
				// 返回到上一个页面
				this.$router.back()
			},
			// list组件上拉触底时触发
			// list组件被页面加载时也会触发
			onLoad() {
				// 得到用户登录的token
				const loginInfo=JSON.parse(window.localStorage.getItem('loginInfo'))
				
				if(loginInfo && loginInfo.token ){
					// 得到用户登录的token
					this.$http({
						// 发送请求到服务器，得到要搜索的文章数据
						url: 'http://ttapi.research.itcast.cn/app/v1_0/search',
						method: "GET",
						headers:{
							Authorization:'Bearer '+loginInfo.token
						},
						params: {
							// 页码
							page:this.page,
							// 页容量
							per_page:this.per_page,
							q: this.$route.query.key
						},
						// 在得到服务器返回的数据之前,使用json-bigint对数据进行处理
						transformResponse:[function(data){
							// data就是从服务器返回来的数据
							return jsonbigint.parse(data)
						}]
					}).then(res => {
						console.log(res.data.data.results)
						// 将得到的数据保存到searchResult,使用拼接的方法，避免结果中永远只有十条数据
						this.searchResult =this.searchResult.concat(res.data.data.results) 
						// 将list的加载状态手动改为false
						this.loading = false
						// 将page+1
						this.page++
						// 判断当前频道下是否存在数据
						if (res.data.data.results.length === 0) {
							// 没有数据，需要将当前频道下的finished改为true，表示所有内容已加载完毕
							this.finished = true
						}
					})
				}else{
					// 没有登录则跳转到登录页面
					this.$router.push('/login')
				}

			},
			// 下拉刷新组件下拉时会触发
			// 下拉刷新，清除当前频道下所有的数据
			onRefresh() {
				// 清除数据
				this.loading = false
				this.isLoading = false
				this.finished = false
				this.searchResult = []
				// 重新加载数据,就会获取新的数据
				this.onLoad()
			},
			// 点击跳转详情页面
			getDetails(art_id){
				this.$router.push('/details?art_id=' + art_id)
			},
			// 点击收藏
			collect(art_id){
				// 得到用户登录的token
				const loginInfo=JSON.parse(window.localStorage.getItem('loginInfo'))
				if(loginInfo && loginInfo.token ){
					this.$http({
						// 发送请求到服务器，得到要搜索的文章数据
						url: 'http://ttapi.research.itcast.cn/app/v1_0/article/collections',
						method: "POST",
						headers:{
							Authorization:'Bearer '+loginInfo.token
						},
						data:{
							target:art_id
						}
					}).then(res => {
						console.log(res.data.message)
						console.log(res.data.data.target)
						this.istarget=res.data.data.target
						this.isCollect=!this.isCollect
					})
				}else{
					// 没有登录则跳转到登录页面
					this.$router.push('/login')
				}
				
			},
		}

	}
</script>

<style scoped>
	.login_nav {
		background-color: #3e9df8;
	}

	/* >>> 深度选择器 */
	.search>>>.van-nav-bar__title {
		color: #fff;
	}

	.search {
		margin-top: 46px;
	}

	.other span {
		margin-right: 10px;
	}

	.search>>>.van-icon-arrow-left {
		color: #fff;
	}
</style>
