<template>
	<div class="channel">
		<!-- 显示频道操作面板 -->
		<!-- 添加一个频道弹出层 -->
		<!-- show：控制面板的显示与隐藏 -->
		<van-popup v-model="show" position="bottom" :style="{ height: '70%' }">
			<!-- 我的频道 -->
			<van-cell title="我的频道">
				<template #default>
					<van-button type="danger" v-if="iconsShow===false" @click="iconsShow=true" class="myeditbtn" plain round size="mini">编辑</van-button>
					<van-button type="danger" v-else @click="iconsShow=false" class="myeditbtn" plain round size="mini">完成</van-button>
				</template>
			</van-cell>
			<!-- 我的频道列表-->
			<van-grid>
				<van-grid-item @click="changeIndex(index)" class="itemf" :class="{active: index===$parent.active}" v-for="(item,index) in $parent.channelList" :key="index">
					<template #text>
						{{item.name}}
						<!--  @click.stop="delChannel(index)" 阻止点击x时，
						事件冒泡到父元素，触发父元素的事件 -->
						<van-icon @click.stop="delChannel(index)" v-if="iconsShow&&index!==0" name="clear" class="icons" />
					</template>
				</van-grid-item>
			</van-grid>
			<!-- 频道推荐 -->
			<van-cell title="频道推荐" />
			<!-- 频道推荐列表-->
			<van-grid>
				<van-grid-item v-for="(item,index) in getTui" :key="index" @click="addChannel(item)">
					<template #text>
						{{item.name}}
					</template>
				</van-grid-item>
			</van-grid>
		</van-popup>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				show: false,
				// 控制×的显示与隐藏
				iconsShow: false,
				allChannel: []
			};
		},
		created() {
			// 得到所有的频道数据
			this.$http({
				url: 'http://ttapi.research.itcast.cn/app/v1_0/channels',
				method: "GET"
			}).then(res => {
				// 保存所有的频道数据
				// console.log(res.data.data.channels)
				this.allChannel = res.data.data.channels
			})
		},
		computed: {
			// 获取推荐频道数据
			getTui() {
				// 通过map筛选出我的频道列表的所有频道id
				var ids = this.$parent.channelList.map(item => {
					return item.id
				})

				// 通过filter筛选出频道id不在ids里面的频道
				var channel = this.allChannel.filter(item => {
					return !ids.includes(item.id)
				})
				return channel
			}
		},
		methods: {
			addChannel(item) {
				// 添加额外属性
				this.$set(item, 'loading', false)
				this.$set(item, 'finished', false)
				this.$set(item, 'isLoading', false)
				this.$set(item, 'articleList', [])
				// 将item添加到我的频道列表中
				// 点击添加之后就会给$parent.channelList(我的频道列表)添加元素，
				// 由于computed的特性是依赖项发生改变就会重新计算，故推荐列表就会改变
				this.$parent.channelList.push(item)

				// 保存修改之后的频道数据
				// 1.判断用户是否登录:浏览器中的localStorage中是否有token
				// 得到用户的登录信息 ->来自登录时保存到本地的数据：loginInfo
				var loginInfo = JSON.parse(window.localStorage.getItem('loginInfo'))
				// 判断用户是否登录
				if (loginInfo && loginInfo.token) {
					// 生成需要提交到服务器中的数据

					// 由于推荐数据是不允许操作的，所以应该从数组中将推荐数据干掉
					// slice(1):取出数组中下标为1到最后的所有元素
					var mychannel = this.$parent.channelList.slice(1).map((item, index) => {
						return {
							id: item.id,
							// seq表示频道列表中元素的排列顺序
							// 由于seq等于1的序号永远是为推荐保留(接口文档的设置)，故需要index+2
							// 由于上边将 推荐 这个频道元素排除在外，故seq要从2开始
							seq: index + 2
						}
					})
					// 登录:将修改后的频道数据提交到服务器
					this.$http({
						url: 'http://ttapi.research.itcast.cn/app/v1_0/user/channels',
						method: "PUT",
						headers: {
							Authorization: 'Bearer ' + loginInfo.token
						},
						data: {
							channels: mychannel
						}
					}).then(res => {
						console.log(res)
						console.log('操作成功')
					})
				} else {
					// 没有登录:将修改后的频道数据( this.$parent.channelList)保存到本地的channels中
					window.localStorage.setItem('channels', JSON.stringify(this.$parent.channelList))
					// 用户未登录时 index.vue会优先从本地的channels属性中取出频道列表赋值给channelList
					// 而channelList就是频道列表和我的频道列表的数据来源

				}
			},
			delChannel(index) {
				// 从我的频道中删除index对应的元素
				this.$parent.channelList.splice(index, 1)
				// 当点击删除时，如果要删除元素在当前选中元素之前，会使当前选中元素的下标-1
				if(index<this.$parent.active){
					this.$parent.active=this.$parent.active-1
				}
				var loginInfo = JSON.parse(window.localStorage.getItem('loginInfo'))
				// 判断用户是否登录
				if (loginInfo && loginInfo.token) {
					var mychannel = this.$parent.channelList.slice(1).map((item, index) => {
						return {
							id: item.id,
							seq: index + 2
						}
					})
					this.$http({
						url: 'http://ttapi.research.itcast.cn/app/v1_0/user/channels',
						method: "PUT",
						headers: {
							Authorization: 'Bearer ' + loginInfo.token
						},
						data: {
							channels: mychannel
						}
					}).then(res => {
						console.log(res)
						console.log('操作成功')
					})
				} else {
					window.localStorage.setItem('channels', JSON.stringify(this.$parent.channelList))

				}
			},
			changeIndex(index){
				// 改变选择频道的下标
				// 修改父组件的active属性,频道列表中控制当前激活的元素的下标
				this.$parent.active=index
			}
		}
	}
</script>

<style scoped>
	.myeditbtn {
		padding: 0px 13px;
	}

	.itemf {
		position: relative;
	}

	.icons {
		position: absolute;
		top: 0px;
		right: 0px;
		color: #666;
	}

	.channel>>>.van-grid-item__content {
		font-size: 12px;
	}
	
	.active{
		color: red;
	}
</style>
