<template>
	<div class="details">
		<!-- 头部导航 -->
		<van-nav-bar left-arrow @click-left="$router.back()" class="login_nav" :fixed="true" title="文章详情" />
		<!-- 标题区域 -->
		<h2>{{details.title}}</h2>
		<!-- 作者区域 -->
		<van-cell>
			<template #title>
				<div class="auth">
					<div class="img">
						<img :src="details.aut_photo" alt="">
					</div>
					<div class="userMsg">
						<div class="username">{{details.aut_name}}</div>
						<div>{{details.pubdate | timeFilter}}</div>
					</div>
					<div class="btn">
						<van-button @click="follow" v-if="details.is_followed===false" type="info">+ 关注</van-button>
						<van-button @click="unfollow" v-else type="info">取消关注</van-button>
					</div>
				</div>
			</template>
		</van-cell>
		<!-- 文章区域 -->
		<van-cell>
			<template #title>
				<div v-html="details.content"></div>
			</template>
		</van-cell>
		<!-- 点赞&不喜欢区域 -->
		<div class="btns">
			<div class="zan">
				<van-button @click="unpraise" plain round icon="like" type="danger" v-if="details.attitude===1">点赞</van-button>
				<van-button @click="praise" plain round icon="like-o" type="default" v-else>点赞</van-button>
			</div>
			<div class="dislike">
				<van-button @click="undislike" plain round icon="delete" type="danger" v-if="details.attitude===0">不喜欢</van-button>
				<van-button @click="dislike" plain round icon="delete" type="default" v-else>不喜欢</van-button>
			</div>
			<div class="collect">
				<van-button @click="uncollect" plain round icon="star" type="danger" v-if="details.is_collected===true">收藏</van-button>
				<van-button @click="collect" plain round icon="star-o" type="default" v-else>收藏</van-button>
			</div>
		</div>
		<!-- 评论区域 -->
		<van-list v-model="loading" :finished="finished" finished-text="没有更多了" @load="onLoad">
			<comment v-for="(item,index) in commentList" :myitem="item" :key="index"></comment>
		</van-list>
		<!-- 添加评论区域 -->
		<div class="write">
			<write></write>
		</div>

	</div>
</template>

<script>
	// 导入子组件
	import write from './com/write.vue'
	import comment from './com/comment.vue'
	export default {
		data() {
			return {
				loading: false,
				finished: false,
				// 保存文章id
				artId:this.$route.query.art_id,
				// 保存文章信息
				details:[],
				// 评论列表
				commentList:[],
				// 定义一个分页的标识
				offset:null,
				// 定义每页的数据量
				limit:10,
				// 定义结束的标识
				endid:0
			};
		},
		// 注册子组件
		components: {
			write:write,
			comment:comment
		},
		methods:{
			onLoad(){
				// 得到当前文章的评论数据
				this.$http({
					url:'http://ttapi.research.itcast.cn/app/v1_0/comments',
					method:'GET',
					params:{
						type:'a',
						source:this.artId,
						offset:this.offset,
						limit:this.limit
					}
				}).then(res => {
					// 赋值数据
					this.offset=res.data.data.last_id
					this.endid=res.data.data.end_id
					this.commentList=this.commentList.concat(res.data.data.results) 
					// 将list 组件的加载状态设置为false
					this.loading=false
					// 判断评论数据是否加载完毕
					if(this.offset===this.endid){
						this.finished=true
					}
					// console.log(res)
					// console.log(this.commentList)
					
				})
			},
			// 取消关注
			unfollow(){
				const myloginInfo = JSON.parse(window.localStorage.getItem('loginInfo'))
				// 得到当前作者的id
				var id = this.details.aut_id
				this.$http({
					url:'http://ttapi.research.itcast.cn/app/v1_0/user/followings/'+id,
					method:'DELETE',
					headers:{
						Authorization:'Bearer '+myloginInfo.token
					}
				}).then(res => {
					console.log(res)
					// 提示用户
					this.$toast.success('取关用户成功')
					// 手动将关注的状态设置为false
					this.details.is_followed=false
				})
			},
			// 关注
			follow(){
				const myloginInfo = JSON.parse(window.localStorage.getItem('loginInfo'))
				// 得到当前作者的id
				var id = this.details.aut_id
				this.$http({
					url:'http://ttapi.research.itcast.cn/app/v1_0/user/followings',
					method:'POST',
					headers:{
						Authorization:'Bearer '+myloginInfo.token
					},
					data:{
						target:id
					}
				}).then(res => {
					console.log(res)
					// 提示用户
					this.$toast.success('关注用户成功')
					// 手动将关注的状态设置为true
					this.details.is_followed=true
				})
				
			},
			// 点赞
			praise(){
				const myloginInfo = JSON.parse(window.localStorage.getItem('loginInfo'))
				this.$http({
					url:'http://ttapi.research.itcast.cn/app/v1_0/article/likings',
					method:'POST',
					headers:{
						Authorization:'Bearer '+myloginInfo.token
					},
					data:{
						target:this.artId
					}
				}).then(res => {
					console.log(res)
					// 提示用户
					this.$toast.success('点赞成功')
					// 手动将作者态度设置为1(点赞)
					this.details.attitude=1
				})
			},
			// 取消点赞
			unpraise(){
				const myloginInfo = JSON.parse(window.localStorage.getItem('loginInfo'))
				this.$http({
					url:'http://ttapi.research.itcast.cn/app/v1_0/article/likings/'+this.artId,
					method:'DELETE',
					headers:{
						Authorization:'Bearer '+myloginInfo.token
					},
				}).then(res => {
					console.log(res)
					// 提示用户
					this.$toast.success('取消点赞成功')
					// 手动将作者态度设置为-1
					this.details.attitude=-1
				})
			},
			// 点击不喜欢
			dislike(){
				const myloginInfo = JSON.parse(window.localStorage.getItem('loginInfo'))
				this.$http({
					url:'http://ttapi.research.itcast.cn/app/v1_0/article/dislikes',
					method:'POST',
					headers:{
						Authorization:'Bearer '+myloginInfo.token
					},
					data:{
						target:this.artId
					}
				}).then(res => {
					console.log(res)
					// 提示用户
					this.$toast.success('已设置为不喜欢')
					// 手动将作者态度设置为0(不喜欢)
					this.details.attitude=0
				})
			},
			// 取消不喜欢
			undislike(){
				const myloginInfo = JSON.parse(window.localStorage.getItem('loginInfo'))
				this.$http({
					url:'http://ttapi.research.itcast.cn/app/v1_0/article/dislikes/'+this.artId,
					method:'DELETE',
					headers:{
						Authorization:'Bearer '+myloginInfo.token
					},
				}).then(res => {
					console.log(res)
					// 提示用户
					this.$toast.success('取消不喜欢成功')
					// 手动将用户态度设置为-1(无态度)
					this.details.attitude=-1
				})
			},
			// 点击收藏
			collect(){
				const myloginInfo = JSON.parse(window.localStorage.getItem('loginInfo'))
				this.$http({
					url:'http://ttapi.research.itcast.cn/app/v1_0/article/collections',
					method:'POST',
					headers:{
						Authorization:'Bearer '+myloginInfo.token
					},
					data:{
						target:this.artId
					}
				}).then(res => {
					console.log(res)
					// 提示用户
					this.$toast.success('收藏成功')
					// 手动将收藏状态设置为true
					this.details.is_collected=true
				})
				
			},
			// 取消收藏
			uncollect(){
				const myloginInfo = JSON.parse(window.localStorage.getItem('loginInfo'))
				this.$http({
					url:'http://ttapi.research.itcast.cn/app/v1_0/article/collections/'+this.artId,
					method:'DELETE',
					headers:{
						Authorization:'Bearer '+myloginInfo.token
					},
				}).then(res => {
					console.log(res)
					// 提示用户
					this.$toast.success('取消收藏成功')
					// 手动将用户态度设置为-1(无态度)
					this.details.is_collected=false
				})
			}
		},
		created() {
			// 获取token
			const myloginInfo = JSON.parse(window.localStorage.getItem('loginInfo'))
			// 向服务器请求获取我的文章详情内容
			this.$http({
				url:'http://ttapi.research.itcast.cn/app/v1_0/articles/'+this.artId,
				method:'GET',
				headers:{
					Authorization:'Bearer '+myloginInfo.token
				}
			}).then(res => {
				this.details=res.data.data
			})
		}
	}
</script>

<style scoped>
	.login_nav {
		background-color: #3e9df8;
	}

	/* >>> 深度选择器 */
	.details>>>.van-nav-bar__title {
		color: #fff;
	}

	.details>>>.van-nav-bar .van-icon {
		color: #fff;
	}

	.details {
		margin-top: 46px;
		margin-bottom: 50px;
	}

	.details h2 {
		text-align: center;
		padding: 20px 0;
	}

	.auth {
		display: flex;
	}

	.auth .img img {
		width: 50px;
		height: 50px;
		border-radius: 50%;
	}

	.auth .userMsg {
		flex: 1;
	}

	.btns {
		display: flex;
		margin: 30px 0;
	}

	.btns .zan,
	.btns .dislike,.btns .collect {
		flex: 1;
		text-align: center;
	}
	.write{
		position: fixed;
		bottom: 0;
		left: 0;
		width: 100%;
	}
</style>
