<template>
	<div class="index">
		<!-- 头部导航 -->
		<van-nav-bar class="login_nav" :fixed="true" title="主页" />
		<!-- 频道区域 -->
		<!-- 通过 v-model 绑定当前激活标签对应的索引值，默认情况下启用第一个标签。 -->
		<van-tabs sticky offset-top="46" v-model="active">
			<van-tab v-for="(item,index) in channelList" :key="index" :title="item.name">
				<!-- 文章区域 -->
				<van-pull-refresh v-model="item.isLoading" @refresh="onRefresh">
					<van-list v-model="item.loading" :finished="item.finished" finished-text="没有更多了" @load="onLoad">
						<van-cell @click.stop="getDetails(subItem.art_id)" v-for="(subItem,subIndex) in item.articleList" :key="subIndex" >
							<template #title>
								<!-- 标题 -->
								<h4>{{subItem.title}}</h4>
								<!-- 图片区域 -->
								<van-grid v-if="subItem.cover.type!==0" :border="false" :column-num="3">
									<van-grid-item v-for="(imageitem,imageindex) in subItem.cover.images" :key="imageindex">
										<van-image :src="imageitem" />
									</van-grid-item>
								</van-grid>
								<!-- 其他信息 -->
								<div class="other">
									<span>{{subItem.aut_name}}</span>
									<span>{{subItem.comm_count}}</span>		
									<span>{{subItem.pubdate | timeFilter}}</span>
								</div>
							</template>
						</van-cell>
						<!-- <van-cell style="height: 150px;" v-for="(subItem,subIndex) in item.articleList" :key="subIndex" :title="subItem.title" /> -->
					</van-list>
				</van-pull-refresh>
			</van-tab>
		</van-tabs>
		<!-- 添加一个容器 -->
		<div class="channelicon" @click="openChannel">
			<van-icon name="bars" />
		</div>
		<!-- 使用channel组件 -->
		<channel ref="mychannel"></channel>
	</div>
</template>

<script>
	// 导入channel组件
	import channel from './com/channel.vue'
	// 导入json-bigint
	import jsonbigint from 'json-bigint'
	export default {
		data() {
			return {
				// 保存频道数据
				channelList: [],
				// 默认激活第一个频道标签，下标为0
				active: 0
			};
		},
		methods: {
			// list组件上拉触底时触发
			// list组件被页面加载时也会触发
			onLoad() {
				// 得到当前选择的频道数据
				const currentChannel = this.channelList[this.active]
				// 得到当前频道的id
				const id = currentChannel.id
				// 得到当前频道对应的文章数据
				this.$http({
					url: 'http://ttapi.research.itcast.cn/app/v1_1/articles',
					method: 'GET',
					params: {
						channel_id: id,
						timestamp: Date.now(),
						with_top: 0
					},
					// 在得到服务器返回的数据之前,使用json-bigint对数据进行处理
					transformResponse:[function(data){
						// data就是从服务器返回来的数据
						return jsonbigint.parse(data)
					}]
				}).then(res => {
					// 将得到的数据保存到当前频道下的articleList
					currentChannel.articleList = currentChannel.articleList.concat(res.data.data.results)
					// 将list的加载状态手动改为false
					currentChannel.loading = false
					// 判断当前频道下是否存在数据
					if (res.data.data.results.length === 0) {
						// 没有数据，需要将当前频道下的finished改为true，表示所有内容已加载完毕
						currentChannel.finished = true
					}
				})

			},
			// 下拉刷新组件下拉时会触发
			// 下拉刷新，清除当前频道下所有的数据
			// 当前的到的数据
			onRefresh() {
				const currentChannel = this.channelList[this.active]
				// 清除数据
				currentChannel.loading = false
				currentChannel.isLoading = false
				currentChannel.finished = false
				currentChannel.articleList = []
				// 重新加载数据
				this.onLoad()

			},
			// 给频道数据源添加额外的属性:loading,finished,isLoading,articleList
			addOtherProp() {
				this.channelList.forEach(item => {
					// 添加额外属性，使用this.$set(被添加的对象,添加的属性,添加的属性对应的值)
					// 这里的this指向data中的channelList,item即为遍历channelList中的每一个元素
					this.$set(item, 'loading', false)
					this.$set(item, 'finished', false)
					this.$set(item, 'isLoading', false)
					this.$set(item, 'articleList', [])

				})
			},
			// 打开频道面板
			openChannel() {
				// 需要得到子组件channel中的show属性
				const mychannel = this.$refs.mychannel
				mychannel.show = true
			},
			getDetails(art_id){
				this.$router.push('/details?art_id=' + art_id)
			}
		},
		// 在页面打开时执行
		created() {
			// 得到token，在登录时保存到localStorage的数据是json字符串，需要转换为对象
			const myloginInfo = JSON.parse(window.localStorage.getItem('loginInfo'))
			// 判断token是否存在，存在则展示当前用户的频道列表
			// token是myloginInfo对象的属性
			if (myloginInfo && myloginInfo.token) {
				this.$http({
					url: 'http://ttapi.research.itcast.cn/app/v1_0/user/channels',
					method: 'GET',
					// 在请求头中携带token
					headers: {
						// Authorization为接口提供的参数，用来保存浏览器传递过去的token
						Authorization: 'Bearer ' + myloginInfo.token
					}
				}).then(res => {
					// 保存数据
					this.channelList = res.data.data.channels
					// 添加额外属性
					this.addOtherProp()

				})
				// 用户未登录
			} else {
				// 从本地中取出数据:假设将来保存操作的频道数据的键为channels
				const mylocalChannel = JSON.parse(window.localStorage.getItem('channels'))
				if (mylocalChannel) {
					// 直接取出本地频道数据
					this.channelList = mylocalChannel
					// 添加额外属性
					this.addOtherProp()
					// 否则在服务器上取出默认数据
				} else {
					this.$http({
						url: 'http://ttapi.research.itcast.cn/app/v1_0/user/channels',
						method: 'GET'
					}).then(res => {
						this.channelList = res.data.data.channels
						// 添加额外属性
						this.addOtherProp()

					})
				}
			}
		},
		// 将channel注册为index的子组件
		components: {
			channel: channel
		}
	}
</script>

<style scoped>
	.login_nav {
		background-color: #3e9df8;
	}

	/* >>> 深度选择器 */
	.index>>>.van-nav-bar__title.van-ellipsis {
		color: #fff;
	}

	/* 头部导航设置为固定定位后会脱离文档流，会使频道区域上浮，设置上边距使头部导航盖住的为空白区域 */
	.index {
		margin-top: 46px;
		margin-bottom: 50px;
	}

	.channelicon {
		position: fixed;
		top: 46px;
		right: 0;
		z-index: 999;
		width: 10%;
		height: 44px;
		text-align: center;
		line-height: 44px;
		background-color: #3e9df8;
	}

	.index>>>.van-tabs__nav {
		width: 90%;
	}

	.other span {
		margin-right: 10px;
	}
</style>
