<template>
	<div>
		<van-search shape="round" left-icon="" v-model="value" show-action @search="onSearch" @cancel="onCancel">
			<template #action>
				<van-button @click="onSearch" type="primary" size="small">发送</van-button>
			</template>
		</van-search>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				value:'',
			}
		},
		methods: {
			// 点击手机上的完成按钮时执行(谷歌浏览器模拟的手机，所以就是点击回车)
			onSearch(){
				// 将用户输入的内容提交到服务器
				const myloginInfo = JSON.parse(window.localStorage.getItem('loginInfo'))
				this.$http({
					url:'http://ttapi.research.itcast.cn/app/v1_0/comments',
					method:'POST',
					headers:{
						Authorization:'Bearer '+myloginInfo.token
					},
					data:{
						target:this.$route.query.art_id,
						content:this.value
					}
				}).then(res => {
					console.log(res)
					// 接收评论的内容
					var comment=res.data.data.new_obj
					// 将新增的评论数据添加到父组件的commentList中
					this.$parent.commentList.unshift(comment)
					this.value=''
				})
			},
			// 点击取消按钮时触发
			onCancel(){
				
			}
		}
	}
</script>

